@import 'vars';

.membership-popup.popup {
    width: 1000px;
    box-sizing: content-box;
    overflow: hidden; 
    .left-block {
        float: left;
        width: 540px;
    }
    .right-block {
        float: right;
        width: 460px;
        padding: 0 30px;
    }
}

.membership-options {
    &__head {
        padding-bottom: 25px;
        margin-bottom: 30px;
    }
    &__title {
        font-size: 30px;
        font-weight: 700;
        text-transform: uppercase;
        text-align: center;
    }
    &__price-block {
        color: #2b2b2b;
        font-family: Roboto;
        font-size: 18px;
        font-weight: 700;
        text-align: center;
        line-height: 19px;
        margin-top: 30px;
        span {
            display: inline-block;
            vertical-align: middle;
        }
    }
    &__price {
    }
    &__price-title {
        color: #d5d5d5;
        font-size: 16px;
        line-height: 16px;
        font-style: italic;
        position: relative;
        padding-left: 20px;
        &:before {
            content: '';
            width: 1px;
            height: 14px;
            top: 50%;
            margin-top: -7px;
            background: #d5d5d5;
            position: absolute;
            left: 10px;
        }
    }
    &__description {
        color: #2b2b2b;
        font-size: 14px;
        font-weight: 400;
    }
    &__selected {
        color: #a6a6a5;
        font-size: 14px;
        font-weight: 400;
        line-height: 12px;
        margin-top: 17px;
        span {
            color: #79a626;
        }
    }
    &--gold {
        .membership-options__title {
            color: $gold-color;
        }
        .membership-options__head {
            border-bottom: 4px solid $gold-color;
        }
    }
    &--platinum {
        .membership-options__title {
            color: $platinum-color;
        }
        .membership-options__head {
            border-bottom: 4px solid $platinum-color;
        }
    }
    &--silver {
        .membership-options__title {
            color: $silver-color;
        }
        .membership-options__head {
            border-bottom: 4px solid $silver-color;
        }
    }
}

.membership-form {
    margin-top: 20px;
    &__fields {
        height: 170px;
        @include clearfix;
    }
    &__checkbox-row {
        width: 50%;
        float: left;
        margin-bottom: 12px;
        position: relative;
        label {
            line-height: 18px;
            cursor: pointer;
            color: #2b2b2b;
            font-size: 14px;
            font-weight: 700;
        }
    }
    &__checkbox {
        line-height: 16px;
    }
}