@import 'mixins';
@import 'popups/membership-popup';

.membership__breadcrumbs {
    margin: 8px 10px 14px;
}

.membership__info {
    text-align: center;
    .page-title {
        margin-top: 15px;
        padding-left: 0;
    }
    .membership__text {
        text-align: left;
        padding: 0 215px;
        margin-top: 20px;
        margin-bottom: 30px;
    }
}

.membership__cards {
    padding: 0 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.card {
    background-color: #fff;
    max-width: 370px;
    padding-bottom: 40px;
    box-shadow: 0 1px 18px 1px rgba(29, 25, 41, 0.08);

    transition: transform .7s;
    &:hover {
        transform: translateY(-7px);
    }
    .card__box-img {
        position: relative;
        height: 100%;
        margin-bottom: 50px;
        .card__bow {
            position: absolute;
            bottom: 8px;
            transform: translate(0, 50%);
        }
    }
    .card-title {
        text-align: center;
        font-size: 30px;
        font-weight: 700;
        text-transform: uppercase;
    }

    .card__price {
        text-align: center;
        margin: 19px 54px 0;
        padding-bottom: 15px;
        .card__price-value {
            color: #2b2b2b;
            font-size: 18px;
            font-weight: 700;
            text-transform: uppercase;
            margin-right: 7px;
        }
    
        .card__price-period {
            box-sizing: border-box;
            color: #999999;
            font-size: 16px;
            line-height: 16px;
            padding: 2px 9px;
            font-style: italic;
            border-left: 2px solid #999;
        }
    }

    .card__desc {
        padding: 29px 33px 0;
    }
    .card__button {
        margin-top: 70px;
        display: flex;
        justify-content: center;
    }  
}

.card--platinum {
    min-width: 400px;
    padding-bottom: 65px;
    box-shadow: 0 1px 18px 1px rgba(29, 25, 41, 0.19);
    .card-title {
        color: $platinum-color;
    }
    .card__bow {
        width: 100%;
    }
    .card__price {
        border-bottom: 4px solid $platinum-color;
    }
}

.card--silver {
    .card-title {
        color: $silver-color;
    }
    .card__price {
        border-bottom: 4px solid $silver-color;
    }
}

.card--gold {
    .card-title {
        color: $gold-color;
    }
    .card__price {
        border-bottom: 4px solid $gold-color;
    }
}

